body {
    margin: 0;
    padding: 0;
    color: #fff;
    font-family: Tahoma, sans-serif;
    background: url("../img/mountain.jpg") no-repeat;
    background-size: cover;
}

* {
    box-sizing: border-box;
}

.clear {
    clear: both;
}

.container {
    margin-right: auto;
    margin-left: auto;
    max-width: 930px;
}

.button {
    display: block;
    padding: 8px;
    color: #fff;
    font-size: 20px;
    text-align: center;
    text-decoration: none;
    border: 2px solid #00a1b3;
    background: linear-gradient(#fdab5a, #b54906);
}

.list-unstyled {
    margin: 0;
    padding: 0;
}

.list-unstyled li {
    list-style: none;
}

.no-resize {
    resize: none;
}

.block-center {
    margin-right: auto;
    margin-left: auto;
}

header {
    margin-bottom: 50px;
}

.block-summary {
    float: left;
    max-width: 380px;
}

.block-summary h1 {
    margin-top: 20px;
    margin-bottom: 25px;
    color: #fff;
    font-size: 48px;
    font-weight: normal;
}

.block-summary p {
    padding-left: 2px;
    color: #83a6ac;
    font-size: 14px;
}

.block-specification {
    float: right;
    margin-top: 30px;
    padding: 0 10px 10px 10px;
    width: 290px;
    border: none;
    background-color: rgba(135, 235, 255, 0.26);
}

.block-specification ul {
    margin: 0 0 10px 0;
}

.block-specification li {
    color: #fff;
    font-size: 18px;
    line-height: 30px;
}

li span {
    color: #b1d0dc;
}

@media (max-width: 700px) {
    .block-summary,
    .block-specification {
        float: none;
        width: 100%;
        max-width: 100%;
    }
}

nav { background-color: rgba(0, 0, 0, 0.75); }

menu {
    display: flex;
    justify-content: space-between;
}

menu a {
    display: inline-block;
    margin: 0 5px;
    padding: 25px 0;
    color: #3bbac2;
    font-size: 24px;
    text-decoration: none;
    border-top: 1px solid transparent;
}

menu li:hover a {
    color: #fdd087;
    border-top: 1px solid #fdd087;
}

@media screen and (max-width: 650px) {
    menu {
        display: block;
    }
}

main {
    padding: 10px;
    background-color: rgba(0, 0, 0, 0.56);
}

h2 {
    margin: 0;
    font-size: 24px;
    font-weight: normal;
    text-align: center;
}

.form-row {
    display: flex;
    margin-top: 20px;
}

.form-col-2 {
    margin-left: 15px;
    width: 380px;
}

.form-col p {
    margin: 0;
    padding: 10px 0 0 0;
}

.form-group-rbl label {
    margin-right: 5px;
}

.form-group,
.form-group-rbl,
.form-group-chbl {
    height: 45px;
}

.form-group label,
.form-group-rbl span,
.form-group-chbl span {
    display: inline-block;
    margin-right: 15px;
    min-width: 93px;
    font-size: 18px;
    text-align: right;
}

.form-group input[type="text"],
.form-group select,
.form-group textarea {
    width: 210px;
}

.form-group-rbl,
.form-group-chbl {
    width: 250px;
}

.form-block {
    margin-bottom: 10px;
    padding: 10px 10px;
    border: 1px solid #00a1b3;
    border-radius: 3px;
    background-color: #b5d5dd;
}

main form .button {
    margin-top: 10px;
    width: 250px;
    cursor: pointer;
}

@media (max-width: 950px) {
    .form-row {
        display: block;
    }

    .form-group textarea {
        width: 337px;
    }

    main form .button {
        margin-top: 20px;
    }
}

.img-ufo-1 {
    position: absolute;
    top: 38px;
    left: 50%;
    margin-left: -45px;
    width: 118px;
    height: 90px;
    animation-name: fly;
    animation-duration: 2s;
    animation-timing-function: ease-out;
    animation-iteration-count: infinite;
}

.img-ufo-2 {
    position: absolute;
    top: 340px;
    left: 50%;
    margin-left: 420px;
    width: 60px;
    height: 45px;
    animation-name: fly;
    animation-duration: 2s;
    animation-timing-function: ease-out;
    animation-iteration-count: infinite;
    animation-delay: 0.5s;
}

@keyframes fly {
    0% {
        margin-top: 0;
    }

    50% {
        margin-top: 10px;
    }

    100% {
        margin-top: 0;
    }
}

@media screen and (max-width: 650px) {
    .img-ufo-1,
    .img-ufo-2 {
        display: none;
    }
}
